.container {
  width: 970px;
  margin: 0 auto;
  padding: 0 20px; }

.invisible {
  visibility: hidden; }

.modal {
  position: fixed;
  top: 150px;
  width: 300px;
  height: 400px;
  background-color: #ffffff;
  box-shadow: 0 20px 40px rgba(0, 1, 1, 0.75); }

.fade {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); }

.screen-readers-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

@font-face {
  font-family: "Exo 2";
  src: url("./fonts/Exo2-Thin-100.woff") format("Woff"), url("./fonts/Exo2-Thin-100.woff2") format("Woff2");
  font-weight: 100;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Trebuchet MS";
  src: url("./fonts/TrebuchetMS-Regular-400.woff") format("Woff"), url("./fonts/TrebuchetMS-Regular-400.woff2") format("Woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Trebuchet MS";
  src: url("./fonts/TrebuchetMS-Bold-700.woff") format("Woff"), url("./fonts/TrebuchetMS-Bold-700.woff2") format("Woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

.page {
  min-width: 320px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-size: 14px;
  line-height: 19px;
  font-weight: 400;
  color: #666666;
  background-color: rgba(0, 0, 0, 0.2);
  background-image: url("./images/page-bg@1x.png"); }
  .page::before, .page::after {
    position: fixed;
    content: "";
    width: 100%;
    height: 30%;
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0)); }
  .page::after {
    bottom: 0;
    background-image: linear-gradient(360deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0)); }
  @media (min-width: 720px) and (max-width: 1120px) {
    .page {
      min-width: 768px; } }
  @media (min-width: 1120px) {
    .page {
      min-width: 1120px; } }

.title {
  font-size: 36px;
  line-height: 43px;
  font-weight: 100;
  color: #ffffff; }

.btn {
  display: inline-block;
  padding: 5px 20px;
  border: 1px solid black;
  border-radius: 100px;
  font-size: calc(10px + 1vmin);
  font-weight: 500;
  text-transform: uppercase;
  color: #222222;
  background-color: #ffffff;
  cursor: pointer; }

@media (min-width: 1120px) {
  .main {
    display: flex;
    align-items: center;
    height: 100vh; } }

.main__title {
  text-align: center; }

.shop {
  position: relative;
  width: 320px;
  margin: 0 auto;
  padding: 38px 80px 65px 80px;
  text-align: center; }
  @media (min-width: 720px) {
    .shop {
      min-width: 720px; } }
  @media (min-width: 1120px) {
    .shop {
      min-width: 1120px; } }
  .shop__title {
    margin-bottom: 35px;
    font-family: "Exo 2", "Trebuchet MS", sans-serif; }
    @media (min-width: 720px) {
      .shop__title {
        margin-bottom: 23px; } }

@media (min-width: 720px) {
  .catalog {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around; } }

.pack__wrapper {
  margin-bottom: 14px; }

.card {
  position: relative;
  z-index: 1;
  width: 320px;
  height: 480px;
  padding: 21px 50px 0 48px;
  border-radius: 12px;
  box-sizing: border-box;
  text-align: left;
  background-image: linear-gradient(135deg, transparent 30px, #1698d9 0);
  cursor: pointer; }
  .card::before {
    position: absolute;
    content: "";
    top: 5px;
    left: 5px;
    z-index: -1;
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    background-image: linear-gradient(135deg, transparent 28px, #ffffff 0); }
  .card--focused {
    background-image: linear-gradient(135deg, transparent 30px, #2ea8e5 0); }
  .card--selected {
    background-image: linear-gradient(135deg, transparent 30px, #d91667 0); }
    .card--selected--focused {
      background-image: linear-gradient(135deg, transparent 30px, #e62e7a 0); }
  .card--disabled {
    color: #b3b3b3;
    background-image: linear-gradient(135deg, transparent 30px, #b3b3b3 0);
    cursor: default; }
    .card--disabled::before {
      background-image: linear-gradient(135deg, transparent 28px, #f2f2f2 0); }
    .card--disabled::after {
      opacity: 0.6; }
  .card__lead {
    padding-bottom: 4px;
    font-size: 16px;
    line-height: 19px; }
    .card__lead--pink {
      text-align: center;
      color: #e62e7a; }
  .card__title {
    font-size: 48px;
    line-height: 56px;
    font-weight: 700;
    color: #000000; }
    .card__title--disabled {
      color: #b3b3b3; }
  .card__filling {
    padding-bottom: 13px;
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
    color: #000000; }
    .card__filling--disabled {
      color: #b3b3b3; }
  .card__set {
    line-height: 16px; }
  .card__weight {
    position: absolute;
    z-index: 1;
    bottom: 20px;
    right: 20px;
    width: 81px;
    height: 81px;
    border-radius: 50%;
    line-height: 22px;
    font-weight: 400;
    color: #ffffff;
    text-align: center;
    background-color: #1698d9; }
    .card__weight--focused {
      background-color: #2ea8e5; }
    .card__weight--selected {
      background-color: #d91667; }
      .card__weight--selected--focused {
        background-color: #e62e7a; }
    .card__weight--disabled {
      background-color: #b3b3b3; }
  .card__quantity {
    padding-top: 22px;
    padding-bottom: 8px;
    font-size: 42px; }
  .card__units {
    font-size: 21px; }
  .card__picture {
    position: absolute;
    top: 208px;
    left: 5px; }
  .card__image {
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px; }
    .card__image--disabled {
      opacity: 0.6; }

.buy {
  position: relative;
  z-index: 1;
  width: 320px;
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.004); }
  .buy__link {
    border-bottom: 1px dashed #1698d9;
    font-size: 13px;
    line-height: 16px;
    font-weight: 400;
    color: #1698d9; }
    .buy__link:hover, .buy__link:focus {
      border-bottom: 1px dashed #2ea8e5;
      color: #2ea8e5; }
  .buy__unavailable {
    color: #ffff66; }
